//하나의 최상위 태그만 써야한다.
//subject가 this 우리가 원하는 걸 출력함

class App extends Component { // class 안에는 strict mode가 적용됨 그래서 this는 undefined가 바잉됨
  constructor(props){
    super(props);
    this.state = {
      mode : 'read',
      subject : {title:'web', sub:'world wid web!'},
      welcome : {title:'Welcom', desc:'Hello, React!!'},
      contents:[
        {id:1, title:'html', desc:'hteml is...'},
        {id:2, title:'css', desc:'css is...'},
        {id:3, title:'javascript', desc:'javascript is...'}
      ]
    }
  }// 가장 먼저 실행됨 초기화 담당
  
  render(){ //props 혹은 state가 변경되면 해당되는 component에 render가 변경된다.
    var _title, _desc = null;
    if(this.state.mode === 'welcome'){
        _title = this.state.welcome.title;
        _desc = this.state.welcome.desc;
    }else if(this.state.mode === 'read'){
        _title = this.state.contents[0].title;
        _desc = this.state.contents[0].desc;
      }
      console.log('render:::', this);
    return(
      <div className = "App">
        <header>
          <h1><a href="/" onClick={function(e){
            console.log('event:::', this);
            e.preventDefault();
            return;
            console.log(e);
            e.preventDefault(); //이벤트 동작 막음
            this.setState({
              mode : 'welcome'
            }) //bind를 안해주면 클릭한 것 window를 가리킨다.
            // 그래서 바인드를 함 app안에 메소드 처럼 동작하기 위한 것

          }.bind(this)}>{this.state.subject.title}</a></h1>
          {this.state.subject.sub}
        </header>
        {/* <Subject title={this.state.subject.title} sub ={this.state.subject.sub}></Subject> */}
        <TOC data={this.state.contents}></TOC>
        <Article title={_title} desc={_desc}></Article>
      </div>
    );
  }
}

export default App;
